import React, { Component } from 'react'
import {css} from 'glamor'
import Globals from './Globals'

import Header from './componentes/Header'
import About from './componentes/about'
import AboutMore from './componentes/AboutMore';
import Palestrantes from './componentes/Team'
import Apresentador from './componentes/Assurance'
import Local from './componentes/Local'
import Patrocinadores from './componentes/Frameworks'
import Construtores from './componentes/Reports'
import Contato from './componentes/Contact'
import Footer from './componentes/Footer'

import background_dots from './imgs/background-dots.png'

const styles = {
  root:css({
    width: '100%',
    background: Globals.colors.black
  }),
  main:css({
    width: '100%',
    background: `url(${background_dots}) repeat left`,
    "backgroundSize": "contain"
  })
}

class App extends Component {
  render() {    
    return (
    <div id="root" {...styles.root}>
      <div {...styles.main}>
        <Header/>
        <About/>
          <AboutMore/>
        <Palestrantes/>
        <Apresentador/>
        <Local/>
        <Patrocinadores/>
        <Construtores/>
        <Contato/>
        <Footer/>
      </div>
    </div>
    );
  }
}

export default App;



